import { _decorator, Component } from 'cc';
const { ccclass } = _decorator;

@ccclass('SingletonComponent')
export class SingletonComponent extends Component {
    private static _instance: SingletonComponent | null = null;

    // Singleton örneğini döndürür
    public static getInstance(): SingletonComponent {
        if (!this._instance) {
            this._instance = new SingletonComponent();
        }
        return this._instance;
    }
}
